import { BIO_BUTTON, BIO_DESCRIPTION } from '../data/constants.js';
import images from '../data/images.js';
import myPDF from '../img/Joaqu√≠nNievaCV.pdf';
import '../styles/BiographyPage.css';

const BiographyPage = () => {
  return (
    <section data-aos="zoom-out" data-aos-duration="800" className="container-bio">
      <div className="img-cont">
        <img className="img-avatar" src={images.perfil} alt="avatar" id="sobre" />
      </div>
      <div className="text-cont">
        <p className="texto">{BIO_DESCRIPTION}</p>
        <a className="cv" href={myPDF} download="Joaqu√≠nNievaCV.pdf">
          {BIO_BUTTON}
        </a>
      </div>
    </section>
  );
};

export default BiographyPage;
